/*
Buttons

Custom bootstrap buttons styling

Markup: html/btn.html

.btn-default     - Button default
.btn-primary     - Button primary
.btn-secondary   - Button secondary
.btn-ctn         - Button call to action
.btn-ctn--border - Button call to action with border
.btn-menu        - BUtton menu

Styleguide buttons
*/
.btn {
  margin-bottom: 1em;
  line-height: 1;
  border-radius: 0;
  border-width: 2px;
  font-weight: 600;
  padding: 0.75em 1em;
  min-height: 2.75em;
  font-size: 0.889em;
  text-decoration: none; }
  .btn:hover, .btn:active, .btn:focus {
    text-decoration: underline;
    -webkit-box-shadow: none;
    box-shadow: none; }
  .btn:focus {
    outline: 2px solid #ffd617;
    border-color: #ffd617; }
  .btn:after {
    line-height: inherit;
    position: static; }
  .btn.btn-default {
    border-color: #004494;
    color: #004494;
    background: none; }
    .btn.btn-default:hover {
      background: #004494;
      color: #fff; }
    .btn.btn-default:active, .btn.btn-default:focus {
      background: #fff;
      color: #004494; }
  .btn.btn-primary {
    border-color: #004494; }
    .btn.btn-primary:hover, .btn.btn-primary:active {
      background: #fff;
      color: #004494; }
    .btn.btn-primary:focus {
      color: #fff;
      background: #004494; }
  .btn.btn-secondary {
    border-color: #fff;
    background: #fff;
    color: #004494; }
  .btn.btn-ctn {
    background: #ffd617;
    color: #000;
    white-space: normal;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    line-height: 1.667; }
    .btn.btn-ctn:hover, .btn.btn-ctn:active {
      border-color: #313cfb;
      background: #fff; }
    .btn.btn-ctn:focus {
      border-color: #313cfb;
      outline-color: #313cfb; }
    .btn.btn-ctn:after {
      margin-left: 10px;
      font-size: 0.8em; }
  .btn.btn-menu {
    border-color: #004494;
    background: #004494;
    color: #fff; }
    .btn.btn-menu:hover, .btn.btn-menu:active, .btn.btn-menu:focus {
      color: #fff; }
    .btn.btn-menu:active, .btn.btn-menu:focus {
      outline: 2px solid #ffd617; }
    .btn.btn-menu:focus {
      border-color: #ffd617;
      outline-color: #ffd617; }
    .btn.btn-menu:after {
      margin-left: 6px !important;
      font-size: 0.45em;
      top: -2px;
      position: relative;
      transition: all 0.3s ease 0s; }
    .btn.btn-menu.is-collapsed:after {
      -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      -o-transform: rotate(180deg);
      transform: rotate(180deg); }

.btn-ctn--border {
  background: #ffd617;
  color: #000;
  border-color: #000; }
  .btn-ctn--border:hover, .btn-ctn--border:active {
    border-color: #000; }
  .btn-ctn--border:focus {
    border-color: #313cfb;
    outline-color: #313cfb; }
  .btn-ctn--border:after {
    display: none; }

.btn--no-margin-bottom * {
  margin-bottom: 0 !important; }

